<template>
	<view class="page-container">
		<!-- 顶部背景 -->
		<view class="bg-header"></view>

		<view class="enterImg">
			<image src="/static/image/enter.png" mode=""></image>
		</view>

		<!-- 内容区 -->
		<view class="form-container">
			<view class="form-title">请填写信息</view>
			<view class="form-item">
				<view class="item-label">水分</view>
				<u-input class="item-input" v-model="formData.name" placeholder="请填写水分" :border="false"></u-input>
			</view>
			<view class="form-item">
				<view class="item-label">灰分</view>
				<u-input class="item-input" v-model="formData.phone" placeholder="请填写灰分" :border="false"
					type="number"></u-input>
			</view>

			<view class="form-item">
				<view class="item-label">总量/吨</view>
				<u-input class="item-input" v-model="formData.company" placeholder="请填写总量/吨" :border="false"></u-input>
			</view>
			<view class="form-item">
				<view class="item-label">总量/亩</view>
				<u-input class="item-input" v-model="formData.mu" placeholder="请填写总量/亩" :border="false"></u-input>
			</view>
			<view class="form-item">
				<view class="item-label">种子品种</view>
				<u-input class="item-input" v-model="formData.seedType" placeholder="请填写种子品种" :border="false"></u-input>
			</view>
			<view class="form-item">
				<view class="item-label">平均高度</view>
				<u-input class="item-input" v-model="formData.avgHeight" placeholder="请填写平均高度"
					:border="false"></u-input>
			</view>
			<view class="form-item">
				<view class="item-label">植株密度</view>
				<u-input class="item-input" v-model="formData.density" placeholder="请填写植株密度" :border="false"></u-input>
			</view>
			<!-- 营养价值RFV单选 -->
			<view class="form-item radio-item">
				<view class="item-label">营养价值RFV</view>
				<view class="radio-group">
					<u-radio-group v-model="formData.rfv" placement="row">
						<u-radio v-for="(item, index) in rfvOptions" :key="index" :name="item.value" :label="item.label" active-color="#65ad66"
							@change="radioChange">
							{{item.label}}
						</u-radio>
					</u-radio-group>
				</view>
			</view>
			<!-- 绿度等级单选 -->
			<view class="form-item radio-item">
				<view class="item-label">绿度等级</view>
				<view class="radio-group">
					<u-radio-group v-model="formData.greenLevel" placement="row">
						<u-radio v-for="(item, index) in greenLevelOptions" :key="index" :name="item.value" active-color="#65ad66"
							:label="item.label">
							{{item.label}}
						</u-radio>
					</u-radio-group>
				</view>
			</view>
			<!-- 茬数单选 -->
			<view class="form-item radio-item">
				<view class="item-label">茬数</view>
				<view class="radio-group">
					<u-radio-group v-model="formData.cutCount" placement="row">
						<u-radio v-for="(item, index) in cutCountOptions" :key="index" :name="item.value" active-color="#65ad66"
							:label="item.label">
							{{item.label}}
						</u-radio>
					</u-radio-group>
				</view>
			</view>
			<!-- 包装单选 -->
			<view class="form-item radio-item">
				<view class="item-label">包装</view>
				<view class="radio-group">
					<u-radio-group v-model="formData.packaging" placement="row">
						<u-radio v-for="(item, index) in packagingOptions" :key="index" :name="item.value" active-color="#65ad66"
							:label="item.label">
							{{item.label}}
						</u-radio>
					</u-radio-group>
				</view>
			</view>
			<view class="form-item textarea-item">
				<view class="item-label">备注</view>
				<u-input class="item-input" v-model="formData.notes" placeholder="请填写备注" :border="false"
					type="textarea"></u-input>
			</view>
		</view>

		<!-- 照片上传区域 -->
		<view class="upload-section">
			<view class="upload-title">请上传照片 <text class="upload-subtitle">(照片不低于5张)</text></view>

			<view class="image-upload-area">
				<!-- 已上传照片展示 -->
				<view class="image-list" v-if="images.length > 0">
					<view class="image-item" v-for="(item, index) in images" :key="index">
						<image :src="item" mode="aspectFill" @click="previewImage(item, images)"></image>
						<view class="delete-icon" @click.stop="deleteImage(index)">
							<u-icon name="close" color="#fff" size="20"></u-icon>
						</view>
					</view>
				</view>

				<!-- 上传按钮 -->
				<view class="upload-button" v-if="images.length < 10" @click="chooseImage()">
					<view class="upload-icon">
						<u-icon name="camera" size="36" color="#c0c4cc"></u-icon>
					</view>
					<view class="upload-text">上传照片</view>
				</view>
			</view>
		</view>

		<!-- 视频上传区域 -->
		<view class="upload-section">
			<view class="upload-title">请上传视频 <text class="upload-subtitle">(视频不低于20秒)</text></view>

			<!-- 视频未上传状态 -->
			<view class="video-upload-button" v-if="!videoUrl" @click="chooseVideo">
				<view class="upload-icon">
					<u-icon name="play-right" size="36" color="#c0c4cc"></u-icon>
				</view>
				<view class="upload-text">上传视频</view>
			</view>

			<!-- 视频已上传状态 -->
			<view class="video-container" v-else>
				<video :src="videoUrl" controls show-center-play-btn show-play-btn initial-time="0" class="video-player"
					:poster="videoPoster"></video>
				<view class="edit-video-btn" @click="chooseVideo">
					<u-icon name="reload" color="#fff" size="24"></u-icon>
					<text class="edit-video-text">更换视频</text>
				</view>
			</view>
		</view>

		<!-- 底部按钮 -->
		<!-- 添加占位区域，防止内容被底部footer遮挡 -->
		<view class="footer-placeholder"></view>

		<view class="footer">
			<u-button class="btn" :custom-style="{border: 'none'}" @click="submitForm">
				提交信息
			</u-button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				formData: {
					name: '',
					phone: '',
					notes: '',
					company: '',
					mu: '',
					seedType: '',
					avgHeight: '',
					density: '',
					rfv: '185', // 营养价值RFV默认值
					greenLevel: '9', // 绿度等级默认值
					cutCount: '1', // 茬数默认值
					packaging: 'large' // 包装默认值
				},
				// 营养价值RFV选项
				rfvOptions: [{
						value: '185',
						label: '指标185'
					},
					{
						value: '170-185',
						label: '指标170-185'
					},
					{
						value: '150-170',
						label: '指标150-170'
					}
				],
				// 绿度等级选项
				greenLevelOptions: [{
						value: '9',
						label: '9级'
					},
					{
						value: '7-8',
						label: '7-8级'
					},
					{
						value: '6-7',
						label: '6-7级'
					},
					{
						value: '4-5',
						label: '4-5级'
					}
				],
				// 茬数选项
				cutCountOptions: [{
						value: '1',
						label: '一茬'
					},
					{
						value: '2',
						label: '二茬'
					},
					{
						value: '3',
						label: '三茬'
					},
					{
						value: '4',
						label: '四茬'
					}
				],
				// 包装选项
				packagingOptions: [{
						value: 'large',
						label: '大包'
					},
					{
						value: 'small',
						label: '小包'
					}
				],
				// 将初始图片列表置空，让用户自己上传
				images: [],
				videoUrl: "", // 视频URL
				videoPoster: "/static/imgCs/1.png", // 视频封面
				videoDuration: 0, // 视频时长（秒）
			};
		},
		onLoad(options) {
			console.log(options)
		},
		onShow() {
			// 设置导航栏背景色与bg-header起始颜色一致
			uni.setNavigationBarColor({
				frontColor: '#ffffff',
				backgroundColor: '#63B872'
			})
		},
		methods: {
			radioChange(e) {
				console.log(e);
			},
			// 选择图片
			chooseImage() {
				// 计算还能选择多少张图片
				const remainCount = 10 - this.images.length;
				if (remainCount <= 0) {
					this.showToast('最多只能上传10张图片');
					return;
				}

				uni.chooseImage({
					count: remainCount, // 最多可以选择的图片张数
					sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], // 从相册选择或使用相机拍摄
					success: (res) => {
						// 将选择的图片添加到图片数组中
						this.images = [...this.images, ...res.tempFilePaths];
						console.log('选择图片成功', this.images);
					},
					fail: (err) => {
						console.error('选择图片失败', err);
					}
				});
			},

			// 删除图片
			deleteImage(index) {
				uni.showModal({
					title: '提示',
					content: '确定要删除这张图片吗？',
					success: (res) => {
						if (res.confirm) {
							this.images.splice(index, 1);
						}
					}
				});
			},

			// 选择视频
			chooseVideo() {
				uni.chooseVideo({
					sourceType: ['album', 'camera'], // 从相册选择或使用相机拍摄
					maxDuration: 60, // 拍摄视频最长时间，单位秒
					camera: 'back',
					success: (res) => {
						console.log('选择视频成功', res);

						// 检查视频时长是否符合要求（不低于20秒）
						if (res.duration < 20) {
							this.showToast('视频时长不能少于20秒，请重新选择');
							return;
						}

						// 更新视频信息
						this.videoUrl = res.tempFilePath;
						this.videoDuration = res.duration;
						// 可以使用视频的封面图作为封面，如果有的话
						if (res.thumbTempFilePath) {
							this.videoPoster = res.thumbTempFilePath;
						}
					},
					fail: (err) => {
						console.error('选择视频失败', err);
					}
				});
			},

			previewImage(current, urls) {
				// 创建完整的URL数组（添加域名前缀，如果是网络图片）
				let imageUrls = [];

				// 处理相对路径图片，确保在小程序中能正确显示
				if (urls && urls.length) {
					imageUrls = [...urls];
				} else {
					// 如果只有一张图片
					imageUrls = [current];
				}

				console.log('预览图片', current, imageUrls);

				// 调用预览API
				uni.previewImage({
					current: current,
					urls: imageUrls,
					success: () => {
						console.log('预览图片成功');
					},
					fail: (err) => {
						console.error('预览图片失败', err);
						uni.showToast({
							title: '图片预览失败',
							icon: 'none'
						});
					}
				});
			},
			submitForm() {
				// 表单验证
				if (!this.formData.name.trim()) {
					this.showToast('请填写水分');
					return;
				}

				if (!this.formData.phone.trim()) {
					this.showToast('请填写灰分');
					return;
				}

				// 验证图片数量
				if (this.images.length < 5) {
					this.showToast('请至少上传5张照片');
					return;
				}

				// 验证视频
				if (!this.videoUrl) {
					this.showToast('请上传视频');
					return;
				}

				// 所有验证通过，提交表单
				uni.showLoading({
					title: '提交中...'
				});

				// 模拟提交过程
				setTimeout(() => {
					uni.hideLoading();
					uni.showToast({
						title: '提交成功',
						icon: 'success'
					});

					// 提交成功后，可以清空表单或跳转页面
					setTimeout(() => {
						// 返回上一页或其他操作
						// uni.navigateBack();
						// 或清空表单
						this.resetForm();
					}, 1500);
				}, 1000);
			},
			showToast(title) {
				uni.showToast({
					title: title,
					icon: 'none'
				});
			},
			resetForm() {
				this.formData = {
					name: '',
					phone: '',
					notes: '',
					company: '',
					mu: '',
					seedType: '',
					avgHeight: '',
					density: '',
					rfv: '185',
					greenLevel: '9',
					cutCount: '1',
					packaging: 'large'
				};
				this.images = [];
				this.videoUrl = '';
				this.videoDuration = 0;
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f5f6f9;
	}

	.footer-placeholder {
		height: 240rpx;
		/* 底部footer的高度+padding */
	}

	/* 上传区域公共样式 */
	.upload-section {
		background-color: #fff;
		border-radius: 16rpx;
		padding: 30rpx;
		margin-bottom: 20rpx;
	}

	.upload-title {
		font-size: 32rpx;
		font-weight: 500;
		color: #333;
		margin-bottom: 20rpx;

		.upload-subtitle {
			font-size: 28rpx;
			font-weight: normal;
			color: #999;
		}
	}

	/* 照片上传样式 */
	.image-upload-area {
		display: flex;
		flex-wrap: wrap;
	}

	.image-list {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
	}

	.image-item {
		width: calc((100% - 30rpx) / 3);
		height: 220rpx;
		margin-right: 15rpx;
		margin-bottom: 15rpx;
		position: relative;

		/* 每行第三个元素不需要右边距 */
		&:nth-child(3n) {
			margin-right: 0;
		}

		image {
			width: 100%;
			height: 100%;
			border-radius: 8rpx;
		}

		.delete-icon {
			position: absolute;
			top: 5rpx;
			right: 5rpx;
			width: 40rpx;
			height: 40rpx;
			background-color: rgba(0, 0, 0, 0.5);
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			z-index: 10;
		}
	}

	.upload-button {
		width: calc((100% - 30rpx) / 3);
		height: 220rpx;
		border: 2rpx dashed #ddd;
		border-radius: 8rpx;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-color: #f8f8f8;
	}

	.upload-icon {
		margin-bottom: 10rpx;
	}

	.upload-text {
		font-size: 26rpx;
		color: #999;
	}

	/* 视频上传样式 */
	.video-upload-button {
		width: 100%;
		height: 300rpx;
		border: 2rpx dashed #ddd;
		border-radius: 8rpx;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-color: #f8f8f8;
	}

	.video-container {
		position: relative;
		width: 100%;
		height: 400rpx;
	}

	.video-player {
		width: 100%;
		height: 100%;
		border-radius: 8rpx;
	}

	.edit-video-btn {
		position: absolute;
		right: 20rpx;
		bottom: 20rpx;
		background-color: rgba(0, 0, 0, 0.5);
		color: #fff;
		display: flex;
		align-items: center;
		padding: 10rpx 20rpx;
		border-radius: 30rpx;

		.edit-video-text {
			font-size: 24rpx;
			margin-left: 10rpx;
		}
	}

	/* 单选按钮样式 */
	.radio-item {
		align-items: flex-start;
		padding: 15rpx 0;
	}

	.radio-group {
		flex: 1;
	}

	::v-deep .u-radio-group {
		justify-content: flex-end;
		flex-wrap: wrap;
	}

	::v-deep .u-radio {
		margin-left: 20rpx;
		margin-bottom: 10rpx;
	}

	::v-deep .u-radio__label {
		font-size: 28rpx;
		color: #606266;
	}

	.slogan {
		padding: 40rpx 0 20rpx 40rpx;
		display: flex;
		flex-direction: column;
		color: #357A3E;
		font-size: 32rpx;
		font-weight: 500;
		line-height: 1.6;
	}

	.enterImg {
		width: 100%;
		height: 190rpx;

		image {
			width: 100%;
			height: 190rpx;
		}
	}

	.bg-header {
		left: 0rpx;
		top: -34rpx;
		width: 100%;
		height: 724rpx;
		opacity: 1;
		border-radius: 0rpx 0rpx, 16rpx, 16rpx;
		background: linear-gradient(180deg, rgba(99, 184, 114, 1) 0%, rgba(245, 246, 249, 1) 100%);
		position: absolute;
		z-index: -1;
	}

	.page-container {
		// position: relative;
		padding: 0 30rpx;
	}

	.form-container {
		margin-top: 0rpx;
		background-color: #fff;
		border-radius: 16rpx;
		padding: 30rpx;
		box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.05);
		margin-bottom: 20rpx;
	}

	.form-title {
		font-size: 32rpx;
		font-weight: 500;
		margin-bottom: 30rpx;
		color: #333;
	}

	.form-item {
		display: flex;
		align-items: center;
		padding: 10rpx 0;
		border-bottom: 2rpx solid #f5f6f9;
	}

	.textarea-item {
		align-items: flex-start;
		padding: 20rpx 0;
	}

	.item-label {
		width: 160rpx;
		font-size: 28rpx;
		color: #333;
	}

	.item-input {
		flex: 1;
		text-align: right;
	}

	.footer {
		display: flex;
		justify-content: space-between;
		padding: 30rpx 30rpx 60rpx 30rpx;
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #fff;
		z-index: 999;
		box-shadow: 0 -2rpx 10rpx rgba(0, 0, 0, 0.05);

		.btn {
			width: 100%;

			::v-deep .u-button {
				border: none !important;
				background: linear-gradient(237.74deg, rgba(114, 186, 112, 1) 0%, rgba(53, 122, 62, 1) 100%);
				height: 88rpx !important;
			}

			button {
				border-radius: 44rpx;
				background: linear-gradient(237.74deg, rgba(114, 186, 112, 1) 0%, rgba(53, 122, 62, 1) 100%);
				border: none !important;
				color: #fff;
				height: 88rpx;
				line-height: 88rpx;
				font-size: 32rpx;
			}
		}
	}
</style>